	module.exports = {"data":{"markdownRemark":{"html":"<p>开法学院发展战略规划课题组的会议，三个系的参加人员都发表了自己的看法，有不少精当的想法。关键是要有思路，没有思路，什么事情也办不成。基本战略是：先确定法学院的内在发展方向，也就是学术发展的方向，主攻的领域和重点突破的领域，然后围绕这个中心来安排全部的资源、管理、教学和科研。尤其是对学生的培养，这是我们教学单位的产品，最后也要到市场上去竞争。人的竞争，恐怕是一切市场竞争中最激烈的、最本质的竞争。</p>\n<p>议论的结果有：</p>\n<p>从法学院的整体来说，可以联合作战的，大致有：政策科学、国际文化(人类文明的历史和现状)、环境科学、行政科学、人权问题、社会发展等;</p>\n<p>从各个系来看，可以发展的方向有：工商人口学、城市社会学、社区发展、社会事业、比较民商法、国际经济法、法律思想史等。</p>\n<p>进一步怎么来发展，要继续思想碰撞，再论证。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0164.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0164.md","frontmatter":{"title":"10月21日 星期五"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0164.md absPath of file >>> MarkdownRemark"}}

